import React from 'react';
import chineseData from '../data/chineseData.json';
import mathData from '../data/mathData.json';
import englishData from '../data/englishData.json';


// 类型断言，确保数据符合预期类型
const chineseKnowledge = chineseData;
const mathKnowledge = mathData;
const englishKnowledge = englishData;

const KnowledgePage = () => {
  const getStatusColor = (status) => {
    switch (status) {
      case 'mastered':
        return '#52c41a'; // 绿色
      case 'learning':
        return '#1890ff'; // 蓝色
      case 'struggling':
        return '#faad14'; // 黄色
      case 'not_started':
        return '#ff4d4f'; // 红色
      default:
        return '#d9d9d9';
    }
  };

  const getStatusText = (status) => {
    switch (status) {
      case 'mastered':
        return '已掌握';
      case 'learning':
        return '学习中';
      case 'struggling':
        return '不掌握';
      case 'not_started':
        return '完全不会';
      default:
        return '未知';
    }
  };

  const renderTable = (data, title) => (
    <div style={{ 
      flex: 1, 
      margin: '0 10px',
      backgroundColor: '#fff',
      borderRadius: '8px',
      padding: '20px',
      boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
    }}>
      <h2 style={{ 
        textAlign: 'center', 
        marginBottom: '20px',
        color: '#333',
        fontSize: '24px',
        fontWeight: 'bold'
      }}>
        {title}
      </h2>
      <table style={{ 
        width: '100%',
        borderCollapse: 'collapse',
        fontSize: '14px'
      }}>
        <thead>
          <tr style={{ backgroundColor: '#f5f5f5' }}>
            <th style={{ 
              padding: '12px 8px',
              textAlign: 'left',
              borderBottom: '2px solid #e8e8e8',
              fontWeight: 'bold',
              color: '#666'
            }}>
              分类
            </th>
            <th style={{ 
              padding: '12px 8px',
              textAlign: 'left',
              borderBottom: '2px solid #e8e8e8',
              fontWeight: 'bold',
              color: '#666'
            }}>
              知识点
            </th>
            <th style={{ 
              padding: '12px 8px',
              textAlign: 'center',
              borderBottom: '2px solid #e8e8e8',
              fontWeight: 'bold',
              color: '#666',
              width: '100px'
            }}>
              学习状态
            </th>
          </tr>
        </thead>
        <tbody>
          {data.map((item) => (
            <tr key={item.id} style={{ 
              borderBottom: '1px solid #f0f0f0',
              transition: 'background-color 0.2s'
            }}>
              <td style={{ 
                padding: '12px 8px',
                color: '#666',
                fontSize: '13px'
              }}>
                {item.category}
              </td>
              <td style={{ 
                padding: '12px 8px',
                color: '#333',
                fontWeight: '500'
              }}>
                {item.knowledge}
              </td>
              <td style={{ 
                padding: '12px 8px',
                textAlign: 'center'
              }}>
                <span style={{
                  backgroundColor: getStatusColor(item.status),
                  color: 'white',
                  padding: '4px 8px',
                  borderRadius: '12px',
                  fontSize: '12px',
                  fontWeight: '500',
                  display: 'inline-block',
                  minWidth: '60px'
                }}>
                  {getStatusText(item.status)}
                </span>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );

  return (
    <div style={{ 
      padding: '20px',
      backgroundColor: '#f5f5f5',
      minHeight: '100vh'
    }}>
      <div style={{ 
        maxWidth: '1400px',
        margin: '0 auto'
      }}>
        <h1 style={{ 
          textAlign: 'center',
          marginBottom: '30px',
          color: '#333',
          fontSize: '32px',
          fontWeight: 'bold'
        }}>
          小学知识点学习状态
        </h1>
        
        <div style={{ 
          display: 'flex',
          gap: '20px',
          alignItems: 'flex-start'
        }}>
          {renderTable(chineseKnowledge, '语文知识点')}
          {renderTable(mathKnowledge, '数学知识点')}
          {renderTable(englishKnowledge, '英语知识点')}
        </div>

        {/* 图例说明 */}
        <div style={{ 
          marginTop: '30px',
          padding: '20px',
          backgroundColor: '#fff',
          borderRadius: '8px',
          boxShadow: '0 2px 8px rgba(0,0,0,0.1)'
        }}>
          <h3 style={{ 
            marginBottom: '15px',
            color: '#333',
            fontSize: '18px'
          }}>
            学习状态说明
          </h3>
          <div style={{ 
            display: 'flex',
            gap: '20px',
            flexWrap: 'wrap'
          }}>
            {[
              { status: 'mastered', text: '已掌握' },
              { status: 'learning', text: '学习中' },
              { status: 'struggling', text: '不掌握' },
              { status: 'not_started', text: '完全不会' }
            ].map((item) => (
              <div key={item.status} style={{ 
                display: 'flex',
                alignItems: 'center',
                gap: '8px'
              }}>
                <span style={{
                  backgroundColor: getStatusColor(item.status),
                  color: 'white',
                  padding: '4px 8px',
                  borderRadius: '12px',
                  fontSize: '12px',
                  fontWeight: '500',
                  minWidth: '60px',
                  textAlign: 'center'
                }}>
                  {item.text}
                </span>
                <span style={{ 
                  color: '#666',
                  fontSize: '14px'
                }}>
                  {item.status === 'mastered' && '知识点已完全掌握'}
                  {item.status === 'learning' && '正在学习中，需要继续练习'}
                  {item.status === 'struggling' && '理解有困难，需要重点关注'}
                  {item.status === 'not_started' && '尚未开始学习'}
                </span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
};

export default KnowledgePage;
