import React, { useState } from 'react';
import { 
  Card, 
  Select, 
  Button, 
  Table, 
  Radio, 
  Space, 
  Typography, 
  message, 
  Row, 
  Col,
  Progress,
  Modal
} from 'antd';
import { hanziData } from '../data/knowledge-base/chinese/hanzi';

const { Title, Text } = Typography;
const { Option } = Select;

const TestHanzi = () => {
  const [difficulty, setDifficulty] = useState('1');
  const [testHanzi, setTestHanzi] = useState([]);
  const [answers, setAnswers] = useState({});
  const [isTestStarted, setIsTestStarted] = useState(false);
  const [isTestCompleted, setIsTestCompleted] = useState(false);
  const [score, setScore] = useState(0);
  const [showResults, setShowResults] = useState(false);

  // 根据难度生成测试题目
  const generateTest = () => {
    const filteredHanzi = hanziData[0].data.filter(
      (item) => item.d3 === difficulty
    );
    
    // 随机选择50个汉字，如果该难度级别不足50个，则全部选择
    const shuffled = [...filteredHanzi].sort(() => 0.5 - Math.random());
    const selected = shuffled.slice(0, Math.min(50, filteredHanzi.length));
    
    setTestHanzi(selected);
    setAnswers({});
    setIsTestStarted(true);
    setIsTestCompleted(false);
    setScore(0);
    setShowResults(false);
  };

  // 处理答案选择
  const handleAnswerChange = (hanzi, value) => {
    setAnswers(prev => ({
      ...prev,
      [hanzi]: value
    }));
  };

  // 计算分数
  const calculateScore = () => {
    const totalQuestions = testHanzi.length;
    const correctAnswers = Object.values(answers).filter(answer => answer === true).length;
    const score = Math.round((correctAnswers / totalQuestions) * 100);
    return score;
  };

  // 完成测试
  const completeTest = () => {
    const unanswered = testHanzi.filter(hanzi => answers[hanzi.d1] === null || answers[hanzi.d1] === undefined);
    
    if (unanswered.length > 0) {
      message.warning(`还有 ${unanswered.length} 个汉字没有选择答案，请完成所有题目后再提交。`);
      return;
    }

    const finalScore = calculateScore();
    setScore(finalScore);
    setIsTestCompleted(true);
    setShowResults(true);
  };

  // 下载测试结果
  const downloadResults = () => {
    const today = new Date().toISOString().split('T')[0]; // YYYY-MM-DD格式
    const filename = `hanzi-${today}.json`;
    
    const results = testHanzi.map(hanzi => ({
      d1: hanzi.d1,
      t1: answers[hanzi.d1] || false
    }));

    const dataStr = JSON.stringify(results, null, 2);
    const dataBlob = new Blob([dataStr], { type: 'application/json' });
    
    const link = document.createElement('a');
    link.href = URL.createObjectURL(dataBlob);
    link.download = filename;
    link.click();
    
    message.success(`测试结果已下载为 ${filename}`);
  };

  // 重新开始测试
  const restartTest = () => {
    setIsTestStarted(false);
    setIsTestCompleted(false);
    setTestHanzi([]);
    setAnswers({});
    setScore(0);
    setShowResults(false);
  };

  // 表格列定义
  const columns = [
    {
      title: '序号',
      key: 'index',
      width: 80,
      render: (_, __, index) => index + 1,
    },
    {
      title: '汉字',
      dataIndex: 'd1',
      key: 'd1',
      width: 100,
      render: (text) => (
        <div style={{ 
          fontSize: '24px', 
          fontWeight: 'bold', 
          textAlign: 'center',
          color: '#1890ff'
        }}>
          {text}
        </div>
      ),
    },
    {
      title: '组词',
      dataIndex: 'd2',
      key: 'd2',
      width: 150,
      render: (text) => (
        <div style={{ fontSize: '16px', textAlign: 'center' }}>
          {text}
        </div>
      ),
    },
    {
      title: '掌握情况',
      key: 'mastery',
      width: 200,
      render: (record) => (
        <Radio.Group
          value={answers[record.d1]}
          onChange={(e) => handleAnswerChange(record.d1, e.target.value)}
          disabled={isTestCompleted}
        >
          <Space direction="vertical">
            <Radio value={true} style={{ color: '#52c41a' }}>
              掌握
            </Radio>
            <Radio value={false} style={{ color: '#ff4d4f' }}>
              不会
            </Radio>
          </Space>
        </Radio.Group>
      ),
    },
  ];

  return (
    <div style={{ padding: '24px', maxWidth: '1200px', margin: '0 auto' }}>
      <Title level={2} style={{ textAlign: 'center', marginBottom: '32px' }}>
        汉字认读测试
      </Title>

      {!isTestStarted ? (
        <Card style={{ marginBottom: '24px' }}>
          <Title level={4}>选择测试难度</Title>
          <Row gutter={16} align="middle">
            <Col>
              <Text strong>难度级别：</Text>
            </Col>
            <Col>
              <Select
                value={difficulty}
                onChange={setDifficulty}
                style={{ width: 120 }}
              >
                <Option value="1">初级 (1级)</Option>
                <Option value="2">中级 (2级)</Option>
                <Option value="3">高级 (3级)</Option>
              </Select>
            </Col>
            <Col>
              <Button type="primary" size="large" onClick={generateTest}>
                开始测试
              </Button>
            </Col>
          </Row>
        </Card>
      ) : (
        <>
          <Card style={{ marginBottom: '24px' }}>
            <Row justify="space-between" align="middle">
              <Col>
                <Text strong>当前难度：{difficulty}级</Text>
                <br />
                <Text type="secondary">
                  题目数量：{testHanzi.length} 个汉字
                </Text>
              </Col>
              <Col>
                <Text strong>完成进度：</Text>
                <Progress
                  percent={Math.round((Object.keys(answers).length / testHanzi.length) * 100)}
                  size="small"
                  style={{ width: 200 }}
                />
              </Col>
              <Col>
                <Space>
                  {!isTestCompleted && (
                    <Button type="primary" onClick={completeTest}>
                      完成测试
                    </Button>
                  )}
                  <Button onClick={restartTest}>
                    重新开始
                  </Button>
                </Space>
              </Col>
            </Row>
          </Card>

          <Card>
            <div style={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
              {Array.from({ length: Math.ceil(testHanzi.length / 10) }, (_, rowIndex) => (
                <div key={rowIndex} style={{ display: 'flex', gap: '16px', justifyContent: 'flex-start' }}>
                  {testHanzi.slice(rowIndex * 10, (rowIndex + 1) * 10).map((hanzi) => (
                    <Card
                      key={hanzi.d1}
                      style={{
                        width: '100px',
                        textAlign: 'center',
                        cursor: 'pointer',
                        backgroundColor: answers[hanzi.d1] === true ? '#f6ffed' : 
                                      answers[hanzi.d1] === false ? '#fff1f0' : '#fff'
                      }}
                      bodyStyle={{ padding: '12px' }}
                      onClick={() => {
                        // 切换选中状态
                        const newAnswers = {...answers};
                        if (answers[hanzi.d1] === undefined) {
                          newAnswers[hanzi.d1] = true;
                        } else if (answers[hanzi.d1] === true) {
                          newAnswers[hanzi.d1] = false;
                        } else {
                          delete newAnswers[hanzi.d1];
                        }
                        setAnswers(newAnswers);
                      }}
                    >
                      <div style={{ fontSize: '24px', marginBottom: '8px' }}>{hanzi.d1}</div>
                      <div style={{ fontSize: '12px', color: '#666' }}>{hanzi.d2}</div>
                      <Radio 
                        checked={answers[hanzi.d1] !== undefined}
                        style={{marginTop: '8px'}}
                      />
                    </Card>
                  ))}
                </div>
              ))}
            </div>
          </Card>

          {/* 结果弹窗 */}
          <Modal
            title="测试结果"
            open={showResults}
            onCancel={() => setShowResults(false)}
            footer={[
              <Button key="download" type="primary" onClick={downloadResults}>
                下载结果
              </Button>,
              <Button key="restart" onClick={restartTest}>
                重新测试
              </Button>,
              <Button key="close" onClick={() => setShowResults(false)}>
                关闭
              </Button>
            ]}
            width={600}
          >
            <div style={{ textAlign: 'center', padding: '20px 0' }}>
              <Title level={1} style={{ color: score >= 80 ? '#52c41a' : score >= 60 ? '#faad14' : '#ff4d4f' }}>
                {score} 分
              </Title>
              <Text style={{ fontSize: '16px' }}>
                {score >= 80 ? '优秀！' : score >= 60 ? '良好！' : '需要继续努力！'}
              </Text>
              <br />
              <Text type="secondary">
                正确掌握：{Object.values(answers).filter(a => a === true).length} 个汉字
              </Text>
              <br />
              <Text type="secondary">
                需要加强：{Object.values(answers).filter(a => a === false).length} 个汉字
              </Text>
            </div>
          </Modal>
        </>
      )}
    </div>
  );
};

export default TestHanzi; 