import React, { useState, useRef } from 'react';
import chineseData from '../data/chineseData.json';
import mathData from '../data/mathData.json';
import englishData from '../data/englishData.json';

const Tester = () => {
  const [selectedSubjects, setSelectedSubjects] = useState(['语文', '数学', '英语']);
  const [difficulty, setDifficulty] = useState('mixed');
  const [questions, setQuestions] = useState([]);
  const [isGenerating, setIsGenerating] = useState(false);
  const [examTitle, setExamTitle] = useState('小学综合测试卷');
  const printRef = useRef(null);

  // 题目模板生成器
  const generateQuestionTemplates = {
    语文: {
      拼音基础: [
        { type: 'choice', template: '选择正确的拼音：___的读音是？', options: ['A. pīn yīn', 'B. pín yīn', 'C. pīng yīn', 'D. píng yīn'], answer: 'A' },
        { type: 'fill', template: '给下面的字注音：汉___', answer: 'hàn' }
      ],
      汉字基础: [
        { type: 'choice', template: '"木"字的笔画数是？', options: ['A. 3画', 'B. 4画', 'C. 5画', 'D. 6画'], answer: 'B' },
        { type: 'fill', template: '"口"字的部首是___', answer: '口' }
      ],
      词汇积累: [
        { type: 'choice', template: '"高兴"的近义词是？', options: ['A. 伤心', 'B. 快乐', 'C. 生气', 'D. 害怕'], answer: 'B' },
        { type: 'fill', template: '"大"的反义词是___', answer: '小' }
      ],
      阅读理解: [
        { type: 'short_answer', template: '阅读短文：小明今天很高兴，因为他帮助了同学。请问小明为什么高兴？', answer: '因为他帮助了同学' }
      ],
      写作表达: [
        { type: 'short_answer', template: '看图写话：请用几句话描述你看到的春天景色。', answer: '春天来了，花儿开了，树叶绿了，小鸟在唱歌。' }
      ],
      古诗文: [
        { type: 'fill', template: '静夜思：床前明月光，疑是___霜。', answer: '地上' }
      ]
    },
    数学: {
      数的认识: [
        { type: 'choice', template: '比5大比8小的数是？', options: ['A. 4', 'B. 6', 'C. 9', 'D. 3'], answer: 'B' },
        { type: 'fill', template: '8由___个一组成', answer: '8' }
      ],
      加减运算: [
        { type: 'fill', template: '5 + 3 = ___', answer: '8' },
        { type: 'fill', template: '9 - 4 = ___', answer: '5' },
        { type: 'choice', template: '12 + 8 = ?', options: ['A. 18', 'B. 19', 'C. 20', 'D. 21'], answer: 'C' }
      ],
      图形认识: [
        { type: 'choice', template: '圆形有几条边？', options: ['A. 0条', 'B. 1条', 'C. 2条', 'D. 3条'], answer: 'A' },
        { type: 'fill', template: '三角形有___个角', answer: '3' }
      ],
      量的测量: [
        { type: 'choice', template: '1米等于多少厘米？', options: ['A. 10厘米', 'B. 50厘米', 'C. 100厘米', 'D. 1000厘米'], answer: 'C' }
      ],
      解决问题: [
        { type: 'short_answer', template: '小明有5个苹果，吃了2个，还剩几个？', answer: '3个' }
      ]
    },
    英语: {
      字母基础: [
        { type: 'choice', template: '字母A的大写形式是？', options: ['A. a', 'B. A', 'C. α', 'D. Α'], answer: 'B' },
        { type: 'fill', template: '写出字母b的大写形式：___', answer: 'B' }
      ],
      词汇积累: [
        { type: 'choice', template: '"红色"用英语怎么说？', options: ['A. blue', 'B. green', 'C. red', 'D. yellow'], answer: 'C' },
        { type: 'fill', template: '"猫"的英文是___', answer: 'cat' }
      ],
      日常交流: [
        { type: 'choice', template: '"你好"用英语怎么说？', options: ['A. Goodbye', 'B. Hello', 'C. Thank you', 'D. Sorry'], answer: 'B' },
        { type: 'fill', template: '"我的名字是..."用英语说：My name is ___', answer: '...' }
      ],
      听力训练: [
        { type: 'short_answer', template: '写出ABC字母歌的前三个字母', answer: 'A B C' }
      ]
    }
  };

  const generateQuestions = () => {
    setIsGenerating(true);
    const allQuestions = [];
    let questionId = 1;

    // 根据选择的科目和难度生成题目
    selectedSubjects.forEach(subject => {
      let data = [];
      switch (subject) {
        case '语文':
          data = chineseData;
          break;
        case '数学':
          data = mathData;
          break;
        case '英语':
          data = englishData;
          break;
      }

      // 根据难度筛选知识点
      let filteredData = data;
      if (difficulty === 'easy') {
        filteredData = data.filter(item => item.status === 'mastered');
      } else if (difficulty === 'medium') {
        filteredData = data.filter(item => item.status === 'learning');
      } else if (difficulty === 'hard') {
        filteredData = data.filter(item => item.status === 'struggling' || item.status === 'not_started');
      }

      // 每个科目生成5-7道题
      const numQuestions = Math.min(6, filteredData.length);
      const selectedKnowledge = filteredData.slice(0, numQuestions);

      selectedKnowledge.forEach(knowledge => {
        const templates = generateQuestionTemplates[subject]?.[knowledge.category] || [];
        if (templates.length > 0) {
          const template = templates[Math.floor(Math.random() * templates.length)];
          const question = {
            id: questionId++,
            subject,
            knowledge: knowledge.knowledge,
            question: template.template,
            options: template.options,
            answer: template.answer,
            type: template.type,
            score: template.type === 'choice' ? 5 : template.type === 'fill' ? 4 : 8
          };
          allQuestions.push(question);
        }
      });
    });

    // 打乱题目顺序
    const shuffledQuestions = allQuestions.sort(() => Math.random() - 0.5);
    setQuestions(shuffledQuestions);
    setIsGenerating(false);
  };

  const handlePrint = () => {
    const printContent = printRef.current;
    if (printContent) {
      const printWindow = window.open('', '_blank');
      if (printWindow) {
        printWindow.document.write(`
          <html>
            <head>
              <title>${examTitle}</title>
              <style>
                body { font-family: 'Microsoft YaHei', Arial, sans-serif; margin: 20px; line-height: 1.6; }
                .exam-header { text-align: center; margin-bottom: 30px; border-bottom: 2px solid #333; padding-bottom: 15px; }
                .exam-title { font-size: 24px; font-weight: bold; margin-bottom: 10px; }
                .exam-info { font-size: 14px; color: #666; }
                .student-info { display: flex; justify-content: space-between; margin: 20px 0; }
                .question { margin: 20px 0; page-break-inside: avoid; }
                .question-header { font-weight: bold; margin-bottom: 10px; }
                .question-content { margin-left: 20px; }
                .options { margin: 10px 0; }
                .option { margin: 5px 0; }
                .answer-space { border-bottom: 1px solid #ccc; min-height: 20px; margin: 10px 0; }
                .short-answer-space { border: 1px solid #ccc; min-height: 60px; margin: 10px 0; padding: 5px; }
                @media print { body { margin: 0; } }
              </style>
            </head>
            <body>
              ${printContent.innerHTML}
            </body>
          </html>
        `);
        printWindow.document.close();
        printWindow.print();
      }
    }
  };

  const totalScore = questions.reduce((sum, q) => sum + q.score, 0);

  return (
    <div className="min-h-screen bg-gray-50">
      {/* 控制面板 */}
      <div className="bg-white shadow-sm border-b p-6">
        <div className="max-w-6xl mx-auto">
          <h1 className="text-3xl font-bold text-gray-900 mb-6">试卷生成器</h1>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">试卷标题</label>
              <input
                type="text"
                value={examTitle}
                onChange={(e) => setExamTitle(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              />
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">选择科目</label>
              <div className="space-y-2">
                {['语文', '数学', '英语'].map(subject => (
                  <label key={subject} className="flex items-center">
                    <input
                      type="checkbox"
                      checked={selectedSubjects.includes(subject)}
                      onChange={(e) => {
                        if (e.target.checked) {
                          setSelectedSubjects([...selectedSubjects, subject]);
                        } else {
                          setSelectedSubjects(selectedSubjects.filter(s => s !== subject));
                        }
                      }}
                      className="mr-2"
                    />
                    {subject}
                  </label>
                ))}
              </div>
            </div>
            
            <div>
              <label className="block text-sm font-medium text-gray-700 mb-2">难度选择</label>
              <select
                value={difficulty}
                onChange={(e) => setDifficulty(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              >
                <option value="mixed">混合难度</option>
                <option value="easy">简单（已掌握）</option>
                <option value="medium">中等（学习中）</option>
                <option value="hard">困难（待加强）</option>
              </select>
            </div>
          </div>
          
          <div className="flex gap-4">
            <button
              onClick={generateQuestions}
              disabled={isGenerating || selectedSubjects.length === 0}
              className="px-6 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 disabled:bg-gray-400 transition-colors"
            >
              {isGenerating ? '生成中...' : '生成试卷'}
            </button>
            
            {questions.length > 0 && (
              <button
                onClick={handlePrint}
                className="px-6 py-2 bg-green-600 text-white rounded-md hover:bg-green-700 transition-colors flex items-center gap-2"
              >
                <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17 17h2a2 2 0 002-2v-4a2 2 0 00-2-2H5a2 2 0 00-2 2v4a2 2 0 002 2h2m2 4h6a2 2 0 002-2v-6a2 2 0 00-2-2H9a2 2 0 00-2 2v6a2 2 0 002 2z" />
                </svg>
                打印试卷
              </button>
            )}
          </div>
        </div>
      </div>

      {/* 试卷显示区域 */}
      {questions.length > 0 && (
        <div className="max-w-4xl mx-auto p-6">
          <div ref={printRef} className="bg-white rounded-lg shadow-lg p-8">
            <div className="exam-header">
              <div className="exam-title">{examTitle}</div>
              <div className="exam-info">
                考试时间：30分钟 &nbsp; 总分：{totalScore}分 &nbsp; 科目：{selectedSubjects.join('、')}
              </div>
            </div>
            
            <div className="student-info">
              <div>姓名：______________</div>
              <div>班级：______________</div>
              <div>学号：______________</div>
              <div>得分：______________</div>
            </div>
            
            <div className="questions-section">
              {questions.map((question, index) => (
                <div key={question.id} className="question">
                  <div className="question-header">
                    {index + 1}. [{question.subject}] {question.question} （{question.score}分）
                  </div>
                  <div className="question-content">
                    {question.type === 'choice' && question.options && (
                      <div className="options">
                        {question.options.map((option, optIndex) => (
                          <div key={optIndex} className="option">{option}</div>
                        ))}
                      </div>
                    )}
                    {question.type === 'fill' && (
                      <div className="answer-space"></div>
                    )}
                    {question.type === 'short_answer' && (
                      <div className="short-answer-space"></div>
                    )}
                  </div>
                </div>
              ))}
            </div>
            
            <div className="mt-8 text-center text-sm text-gray-500">
              预计完成时间：30分钟 | 请仔细审题，认真作答
            </div>
          </div>
        </div>
      )}
      
      {questions.length === 0 && !isGenerating && (
        <div className="max-w-4xl mx-auto p-6">
          <div className="bg-white rounded-lg shadow-lg p-8 text-center">
            <div className="text-gray-500 text-lg">
              请选择科目和难度，然后点击"生成试卷"开始创建试卷
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default Tester;
